"use client";

export default function StockReportTable({ inventory }: { inventory: any[] }) {
    return (
        <div className="bg-white border rounded-xl overflow-x-auto">
            <table className="w-full text-sm border border-gray-200">
                <thead className="bg-gray-200">
                    <tr>
                        <th className="px-3 py-2 border-r text-left">#</th>
                        <th className="px-3 py-2 border-r text-left">Product</th>
                        <th className="px-3 py-2 border-r text-center">SKU</th>
                        <th className="px-3 py-2 border-r text-center">Pack Size</th>
                        <th className="px-3 py-2 border-r text-center">Weight</th>
                        <th className="px-3 py-2 border-r text-center">Location</th>
                        <th className="px-3 py-2 border-r text-center">Quantity</th>
                        <th className="px-3 py-2 border-r text-center">Tonnage (kg)</th>
                        <th className="px-3 py-2 border-r text-center">Value (K)</th>
                    </tr>
                </thead>
                <tbody className="divide-y divide-gray-300">
                    {inventory.map((item, idx) => (
                        <tr key={item.id} className={idx % 2 === 0 ? "bg-white" : "bg-gray-50"}>
                            <td className="px-3 py-2 border-r">{idx + 1}</td>
                            <td className="px-3 py-2 border-r">{item.product.name}</td>
                            <td className="px-3 py-2 border-r text-center">{item.product.sku}</td>
                            <td className="px-3 py-2 border-r text-center">{item.product.packSize}</td>
                            <td className="px-3 py-2 border-r text-center">
                                {item.product.weightValue} {item.product.weightUnit}
                            </td>
                            <td className="px-3 py-2 border-r text-center">{item.location.name}</td>
                            <td className="px-3 py-2 border-r text-center">{item.quantity}</td>
                            <td className="px-3 py-2 border-r text-center">{item.tonnage.toFixed(2)}</td>
                            <td className="px-3 py-2 border-r text-center">{item.value.toFixed(2)}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}
