"use client";

import Sidebar from "@/components/sidebar2";
import { getCurrentUser } from "@/lib/auth";
import { prisma } from "@/lib/prisma";
import StockReportTable from "./StockReportTable";
import DateFiltersExports from "./DateFiltersExports";
import { useState, useMemo } from "react";
import { unitToKg } from "@/lib/UnitToKg";

export default async function StockReportPage() {
    const user = await getCurrentUser();
    if (!user) return <p>Unauthorized</p>;

    const inventory = await prisma.inventory.findMany({
        where: { createdBy: user.id },
        include: { product: true, location: true },
        orderBy: { createdAt: "desc" },
    });

    const computedInventory = inventory.map((item) => ({
        ...item,
        tonnage: (item.quantity * (item.product.packSize || 1) * unitToKg(item.product.weightValue || 0, item.product.weightUnit || "kg")) / 1000,
        value: item.quantity * Number(item.product.price || 0),
    }));

    // Options for filters
    const locationOptions = [...new Set(computedInventory.map(i => i.location.name))].map(l => ({ value: l, label: l }));
    const categoryOptions = [...new Set(computedInventory.map(i => i.product.category))].map(c => ({ value: c, label: c }));
    const productOptions = computedInventory.map(i => ({ value: i.product.id, label: i.product.name, category: i.product.category }));

    // Filter state
    const [startDate, setStartDate] = useState<Date | null>(null);
    const [endDate, setEndDate] = useState<Date | null>(null);
    const [selectedLocations, setSelectedLocations] = useState<string[]>([]);
    const [selectedCategories, setSelectedCategories] = useState<string[]>([]);
    const [selectedProducts, setSelectedProducts] = useState<string[]>([]);

    // Filtered inventory
    const filteredInventory = useMemo(() => {
        return computedInventory.filter(item => {
            const inLocation = selectedLocations.length === 0 || selectedLocations.includes(item.location.name);
            const inCategory = selectedCategories.length === 0 || selectedCategories.includes(item.product.category);
            const inProduct = selectedProducts.length === 0 || selectedProducts.includes(item.product.id);
            const inStartDate = !startDate || new Date(item.createdAt) >= startDate;
            const inEndDate = !endDate || new Date(item.createdAt) <= endDate;

            return inLocation && inCategory && inProduct && inStartDate && inEndDate;
        });
    }, [computedInventory, selectedLocations, selectedCategories, selectedProducts, startDate, endDate]);

    const exportCSV = () => {
        const header = ["Product", "SKU", "Category", "Location", "Qty", "Tonnage (kg)", "Value"];
        const rows = filteredInventory.map(i => [
            i.product.name,
            i.product.sku,
            i.product.category,
            i.location.name,
            i.quantity,
            i.tonnage.toFixed(2),
            i.value.toFixed(2),
        ]);
        const csvContent = [header, ...rows].map(e => e.join(",")).join("\n");
        const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });
        const url = URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "stock-report.csv");
        link.click();
    };

    const exportPDF = () => {
        // Optional: implement PDF export using jsPDF/autotable
        alert("PDF export not implemented yet.");
    };

    return (
        <div className="min-h-screen bg-gray-50">
            <Sidebar currentPath="/inventory/stock-report" />
            <main className="ml-64 p-8">
                <h1 className="text-2xl font-semibold mb-4">Stock Report</h1>

                <DateFiltersExports
                    view="stock"
                    startDate={startDate}
                    endDate={endDate}
                    setStartDate={setStartDate}
                    setEndDate={setEndDate}
                    selectedLocations={selectedLocations}
                    setSelectedLocations={setSelectedLocations}
                    selectedCategories={selectedCategories}
                    setSelectedCategories={setSelectedCategories}
                    selectedProducts={selectedProducts}
                    setSelectedProducts={setSelectedProducts}
                    locationOptions={locationOptions}
                    categoryOptions={categoryOptions}
                    productOptions={productOptions}
                    exportCSV={exportCSV}
                    exportPDF={exportPDF}
                    setView={() => { }}
                />

                <StockReportTable inventory={filteredInventory} />
            </main>
        </div>
    );
}
